.random-word {
  display: flex;
  flex-direction: column;
}

.random-word button {
  align-self: flex-start;
  display: inline-block;
  float: none;
}

.testLink {
  cursor: pointer;
  font-weight: bold;
  color: blue;
}

.clickable {
  cursor: pointer;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.clickable:hover {
  /* border-radius: 40%; */
  background-color: white;
}

.word-heading {
  font-size: 1.6rem;
  /* color: green; */
  -webkit-animation: fade-in 1s ease;
          animation: fade-in 1s ease;
}

.word {
  font-size: 1.4rem;
  margin-left: 1rem;
}

.pos {
  font-size: 1.2rem;
  margin-left: 1rem;
}
span {
  /* display: flex;
  justify-content: space-between; */
}
.add-button {
  float: right;
}

.remove-button {
  float: right;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
    /* transform: translateY(20px); */
  }
  50% {
    opacity: 1;
    color: rgb(0, 60, 255);
    /* transform: translateY(0); */
  }
  100% {
    color: black;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
    /* transform: translateY(20px); */
  }
  50% {
    opacity: 1;
    color: rgb(0, 60, 255);
    /* transform: translateY(0); */
  }
  100% {
    color: black;
  }
}

